import React from 'react';
import '../assets/scss/main.scss';
import Button from './Button';
import { faFilter } from '@fortawesome/free-solid-svg-icons';
import Dropdown from './Dropdown';
import Service from './Service';

interface iContainerProps {
  services: string[][];
  title: string;
}


class News extends React.Component<iContainerProps> {

  constructor(props: iContainerProps) {
    super(props);
  }

  public render() {
    return (
      <div className="container-fluid" id="containerEnvelope">
        <div className="row">
          <div className="col-7">
            <h1>{this.props.title}</h1>
          </div>
          <div className="col">
            <div id="bottomPart" className="form-inline my-2 my-lg-0">
              <input className="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search" />
              <button className="btn btn-outline-success my-2 my-sm-0" type ="submit">Search</button>
            </div>
          </div>
          <div className="col">
            <Dropdown options={['Option1', 'Option1']} title={''} collapsed={true} icon={faFilter} />
          </div>
        </div>
        <div className="row">
          {this.props.services.map((s, i) => (
            <div className="col">
              <Service title={s[0]} description={s[1]} imageUrl={s[2]} />
            </div>
          ))}
        </div>
      </div>
    );
  }
}

export default News;